@media print {
  /* your print styles */
  .raleigh-campaign.Campaign.CampaignType--floating div,
  .site-header .header-right-column,
  #page .site-main .post-nav-wrapper,
  #page .site-main .social-share-wrapper,
  aside.sidebar .location-callout-box-basic,
  aside.sidebar .featured-posts-block,
  aside.sidebar .categories-block,
  #page .site-main .call-to-action,
  footer.primary-footer {
    display: none;
  }
}

.hero {
  background-image: url(../../img/pattern/patt_4_blue.svg) !important;
  background-color: #084C61;
  background-repeat: repeat;
  background-size: 30px auto;
  color: white;
}

.hero .center-items {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero h2 {
  color: white !important;
}

h1 {
  font-weight: bold;
  color: #fff;
  font-size: 56px;
}

.lifestyle-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.lifestyle-section h2 {
  font-size: 42px;
  margin-bottom: 42px;
}

.lifestyle-section .row {
  justify-content: space-between !important;
  width: 100%;
  margin: 0 !important;
}

.lifestyle-section .row .guide-card {
  max-width: 380px;
  box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.09);
  padding: 56px;
  text-align: left;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.lifestyle-section .row .guide-card .guide-title {
  text-align: center !important;
  font-size: 28px !important;
  margin-bottom: 26px;
}

.lifestyle-section .row .guide-card .guide-link {
  text-decoration: underline;
}

.lifestyle-section .row .guide-card .guide-image {
  border-radius: 20px;
  border-left: 8px solid #084C61;
}

.lifestyle-section .row .article-title {
  text-align: center;
  font-size: 16px !important;
  font-family: 'Gotham' !important;
  font-weight: bold !important;
  background: #084C61;
  color: white;
  margin: 0;
}

.loc-quiz {
  background-color: #084C61;
  background-image: url(../../img/pattern/patt_4_blue.svg) !important;
  background-repeat: repeat;
  background-size: 30px auto;
}

.loc-quiz .loc-card {
  background-color: white;
  border-radius: 15px;
  padding: 16px;
}

.loc-quiz .loc-card .card-title {
  color: black !important;
}

.loc-quiz .loc-card .card-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.loc-quiz .loc-card .card-img {
  border-radius: 0 0 25px 25px;
}

@media (min-width: 768px) {
  .loc-quiz .loc-card .card-img {
    border-radius: 0 25px 25px 0;
  }
}

.fp-block {
  overflow-x: hidden;
}

.fp-block h2 {
  font-size: 42px;
}

.fp-block .fp-title:after {
  background: #177E89;
  content: "";
  height: 3px;
  margin-left: 1rem;
  position: absolute;
  margin-top: 30px;
  margin-left: 170px;
  width: 100%;
}

.fp-block .fp-decorative-img {
  position: absolute;
  width: 70px;
  height: auto;
  left: -50px;
  top: -20px;
  transform: rotate(-122deg);
}

.fp-block .fp-img {
  border-radius: 25px;
}

.fp-block .fp-content-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.section-find-community.style-alt {
  background-color: #E9E7EE !important;
  background-image: url(../../img/pattern/patt_7_white.svg) !important;
  background-repeat: repeat !important;
  background-size: 30px auto !important;
}

.section-find-community.style-alt .row {
  background: white !important;
  color: black !important;
}

.section-find-community.style-alt .row h3, .section-find-community.style-alt .row p {
  color: black;
}

@media (max-width: 768px) {
  .section-find-community.style-alt .row {
    padding: 16px;
  }
}

@media (min-width: 768px) {
  .section-find-community.style-alt {
    padding: 4rem;
  }
}

@media (max-width: 768px) {
  h1 {
    font-size: 36px !important;
  }
  h2 {
    font-size: 30px !important;
  }
  .hero .py-6 {
    padding: 30px 0 !important;
  }
  .guide-card {
    padding: 0 !important;
    box-shadow: unset !important;
    margin: 15px !important;
    text-align: center !important;
  }
  .fp-decorative-img {
    display: none;
  }
  .article-image {
    width: 100% !important;
  }
}
